import React from "react"
import Helmet from "react-helmet"
import Layout from "../components/layout"

function _404() {
    return (
        <Layout>
            <Helmet
                bodyAttributes={{
                    class: "page _404",
                }}
            />

            <section>
                Sorry, that page doesn't exist
            </section>

        </Layout>
    )
}

export default _404